@import url('https://fonts.googleapis.com/css2?family=Mulish&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap');

* {
  margin:0;
  padding:0;
  box-sizing: border-box;
  font-family: 'Mulish', sans-serif;
}

.navbar-brand {
  font-size:1.9rem !important;
  color: #565387 !important;
}


 .navbar a {
  font-size:1.3rem;
  text-transform: capitalize;
  color:#fff;
 }

.navbar a:hover {
  color: #3498db !important;
}


.navbar-nav {
  margin-left:auto !important;
}
.menu_active {
  font-weight:bold;
  border-bottom:1px solid #565387;
}

@media (max-width: 991px) {
  .navbar-brand {
    font-size:1.5rem !important;
  }
  .menu_active {
    font-weight:bold;
    border-bottom:none;
  }
}

/* Main Section */

#header {
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}
#header h1 {
  margin: 0 0 10px 0;
  font-size:42px;
  font-weight:700;
  color:black;
}
#header .brand-name {
  color: #3498db;
}
#header h2 {
  color:#484848;
  font-size: 24px;
}
#header .btn-get-started {
  font-weight: 500;
  font-size:16px;
  letter-spacing:1px;
  display:inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition:0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}
#header .btn-get-started:hover {
  background:#3498db;
  color:#fff;
}
#header-img {
  text-align: right;
}
#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down {
  0% { 
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
@keyframes up-down {
  0% { 
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
/* Service page */

.tab-center {
text-align: center;
}
footer p {
  padding:0;
  margin:0;
  position:absolute;
  bottom:0;
  background-color:#EAF4F5;
  width:100%;
  position:sticky;
}
/* responsive code */
@media (max-width:991px) {
  #header {
    height: 80vh;
    text-align:center;
  }
  #header .header-img {
    text-align: center;
  }
  #header .header-img img {
    width:60%;
  }
}

@media (max-width:768px) {
  #header {
    margin-top: 20px;
  }
  #header h1 {
    font-size: 28px;
  }
  #header h2 {
    font-size: 18px;
    margin-bottom: 30px;
  }
  #header .header-img img {
    width: 70%;
  }
}
.profile {
  padding:30px 40px;
  padding-bottom: 70px;
  margin:0 10px;
  display: flex;

}
.profile img {
  padding:5px ;
  margin:25px 3px;
  height:220px;
  width:220px;
  border:1px solid #3498db;
  border-radius:15%;
}
.profile p {
  display: flex;
  font-family: 'Indie Flower', cursive;
  margin:50px 25px;
  font-size:20px;
  font-weight: 500;
  width:100%;

}
.about h3 {
  font-weight: 600;
  font-size:30px;
  display:inline-block;
  border-bottom:1px solid #3498db;
  border-bottom-style:ridge;
}

/* animation effect */

h1 .brand-name {
  position: relative;
  color: #fff !important;
}
h1 .brand-name::before {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width:0;
  height:100%;
  color: #4EC1FC ;
  /* color: #5a01fe; */
  overflow:hidden;
  animation:animate 5s linear infinite;
  }
  @keyframes animate {
  0%,10%,100% {
      width: 0;
  }
  70%,90% {
      width: 110%;
  }
  }
  
  .card img {
  opacity:1;
  display:block;
  transition: .5s ease;
  backface-visibility: hidden;
  }
  .card:hover {
   opacity: 0.6;
  }